import React from "react";
import "./Projects.scss";
import { motion } from "framer-motion";

function Projects() {


  // transition
  const transition1 = {
    duration: 1,
    type: "linear",
  };

  const transition2 = {
    delay: .1,
    duration: .8,
    type: "tween",
  };


  return (
    <>
      <section id="projects" className="projects Projects">
        <div className="projectwrap">
          <motion.h1 className="secHead"
          initial={{ x: -50 }}
          whileInView={{ x: 0 }}
          transition={transition1}
          >&#60;Latest Projects&#62;</motion.h1>
          <ul className="projectsList clearfix" id="projectsList">
            <li >
              <motion.article 
              className="projList"
              initial={{ y: 200 }}
              whileInView={{ y: 0}}
              transition={transition2}
              >
                <h1 className="projListName ">My Portfolio Website</h1>
                <h2 className="projListDesc">
                  Portfolio Website made with React. A high-performance, Responsive website that uses ScrollMagic powered animations for enriching user experience.
                  <a href="https://nickdl29.github.io/nick-portfolio/" target="_blank" rel="noopener noreferrer">
                    Case Study
                  </a>
                </h2>
              </motion.article>
              <motion.div
              initial={{ scaleX: 1 }}
              whileInView={{ scaleX: 0}}
              transition={transition1}
                className="revealer">
                </motion.div>
            </li>
            <li >
            <motion.article 
              className="projList"
              initial={{ y: 200 }}
              whileInView={{ y: 0}}
              transition={transition2}
              >
                <h1 className="projListName ">Automatic Attendance System via Facial Recognition</h1>
                <h2 className="projListDesc">
                PROJECT DEFINITION:- We are making an Automatic Attendance System via Facial Recognition (AASFR). Our main goal is to make a system where students' attendance is automatically taken by recognizing their faces. It will also help teachers and students to keep track of their attendance as well.
                  <a
                    href="https://github.com/NickDL29/AASFR-FaceRecognition"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    GitHub Repo
                  </a>
                </h2>
              </motion.article>
              <motion.div
              initial={{ scaleX: 1 }}
              whileInView={{ scaleX: 0}}
              transition={transition1}
                className="revealer">
                </motion.div>
            </li>
            <li >
            <motion.article 
              className="projList"
              initial={{ y: 200 }}
              whileInView={{ y: 0}}
              transition={transition2}
              >
                <h1 className="projListName ">Restaurant Landing Page</h1>
                <h2 className="projListDesc">
                  Lorem ipsum dolor, sit amet consectetur adipisicing elit. Molestiae quam placeat sunt perferendis officia suscipit eveniet iusto soluta sequi voluptatibus?
                  <a
                    href="https://nickdl29.github.io/nick-portfolio/"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Visit Website
                  </a>
                </h2>
              </motion.article>
              <motion.div
              initial={{ scaleX: 1 }}
              whileInView={{ scaleX: 0}}
              transition={transition1}
                className="revealer">
                </motion.div>
            </li>
          </ul>
          {/* <div className="secEnd" data-text="Latest Projects"></div> */}
          <motion.h1 className="secEnd"
          initial={{ x: -50 }}
          whileInView={{ x: 0 }}
          transition={transition1}
          >&#60;/Latest Projects&#62;</motion.h1>
        </div>
      </section>
    </>
  );
}

export default Projects;
